import { Fragment, useState, useEffect } from 'react';
import { Dialog, Transition } from '@headlessui/react';
import { XMarkIcon } from '@heroicons/react/24/outline';
import { Request } from '@/lib/types';
import { api } from '@/lib/api/client';

interface RequestModalProps {
    isOpen: boolean;
    onClose: () => void;
    request: Request | null;
    onUpdate: () => void;
}

export function RequestModal({ isOpen, onClose, request, onUpdate }: RequestModalProps) {
    const [formData, setFormData] = useState<Partial<Request>>({});
    const [isCreating, setIsCreating] = useState(false);

    useEffect(() => {
        if (request) {
            setFormData(request);
            setIsCreating(false);
        } else {
            setFormData({
                source_tag: 'manual',
                status: 'Under Review'
            });
            setIsCreating(true);
        }
    }, [request, isOpen]);

    const handleSave = async () => {
        try {
            if (isCreating) {
                await api.createRequest(formData);
            } else if (request) {
                await api.updateRequest(request.id, formData);
            }
            onUpdate();
            onClose();
        } catch (error) {
            console.error('Failed to save request', error);
        }
    };

    const handleApprove = async () => {
        if (!request) return;
        try {
            await api.approveRequest(request.id);
            onUpdate();
            onClose();
        } catch (error) {
            console.error('Failed to approve request', error);
        }
    };

    const handleReject = async () => {
        if (!request) return;
        try {
            await api.rejectRequest(request.id);
            onUpdate();
            onClose();
        } catch (error) {
            console.error('Failed to reject request', error);
        }
    };

    return (
        <Transition.Root show={isOpen} as={Fragment}>
            <Dialog as="div" className="relative z-50" onClose={onClose}>
                <Transition.Child
                    as={Fragment}
                    enter="ease-out duration-300"
                    enterFrom="opacity-0"
                    enterTo="opacity-100"
                    leave="ease-in duration-200"
                    leaveFrom="opacity-100"
                    leaveTo="opacity-0"
                >
                    <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
                </Transition.Child>

                <div className="fixed inset-0 z-10 overflow-y-auto">
                    <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
                        <Transition.Child
                            as={Fragment}
                            enter="ease-out duration-300"
                            enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                            enterTo="opacity-100 translate-y-0 sm:scale-100"
                            leave="ease-in duration-200"
                            leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                            leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                        >
                            <Dialog.Panel className="relative transform overflow-hidden rounded-lg bg-white px-4 pb-4 pt-5 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-4xl sm:p-6">
                                <div className="absolute right-0 top-0 hidden pr-4 pt-4 sm:block">
                                    <button
                                        type="button"
                                        className="rounded-md bg-white text-gray-400 hover:text-gray-500 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                                        onClick={onClose}
                                    >
                                        <span className="sr-only">Close</span>
                                        <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                                    </button>
                                </div>

                                <div className="sm:flex sm:items-start w-full">
                                    <div className="mt-3 text-center sm:ml-4 sm:mt-0 sm:text-left w-full">
                                        <Dialog.Title as="h3" className="text-base font-semibold leading-6 text-gray-900">
                                            {isCreating ? 'New Request' : `Request Details: ${request?.summary}`}
                                        </Dialog.Title>

                                        <div className="mt-4">
                                            {isCreating ? (
                                                <div className="space-y-6">
                                                    {/* Context Input */}
                                                    <div>
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">
                                                            Request Context
                                                        </label>
                                                        <p className="mt-1 text-sm text-gray-500">
                                                            Describe the task or request in detail. The AI agent will extract the necessary information.
                                                        </p>
                                                        <div className="mt-2">
                                                            <textarea
                                                                rows={6}
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                placeholder="e.g. Please create a JIRA ticket for fixing the login bug reported by Sarah. See attached screenshot..."
                                                                value={formData.description || ''}
                                                                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                                                            />
                                                        </div>
                                                    </div>

                                                    {/* Upload Assets */}
                                                    <div>
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">
                                                            Assets
                                                        </label>
                                                        <div className="mt-2 flex justify-center rounded-lg border border-dashed border-gray-900/25 px-6 py-10">
                                                            <div className="text-center">
                                                                <div className="mt-4 flex text-sm leading-6 text-gray-600">
                                                                    <label
                                                                        htmlFor="file-upload"
                                                                        className="relative cursor-pointer rounded-md bg-white font-semibold text-indigo-600 focus-within:outline-none focus-within:ring-2 focus-within:ring-indigo-600 focus-within:ring-offset-2 hover:text-indigo-500"
                                                                    >
                                                                        <span>Upload files</span>
                                                                        <input id="file-upload" name="file-upload" type="file" className="sr-only" multiple />
                                                                    </label>
                                                                    <p className="pl-1">or drag and drop</p>
                                                                </div>
                                                                <p className="text-xs leading-5 text-gray-600">Emails, Screenshots, Documents</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            ) : (
                                                <div className="grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                                                    {/* Summary */}
                                                    <div className="sm:col-span-6">
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">Summary</label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                value={formData.summary || ''}
                                                                onChange={(e) => setFormData({ ...formData, summary: e.target.value })}
                                                            />
                                                        </div>
                                                    </div>

                                                    {/* Description */}
                                                    <div className="sm:col-span-6">
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">Description</label>
                                                        <div className="mt-2">
                                                            <textarea
                                                                rows={4}
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                value={formData.description || ''}
                                                                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                                                            />
                                                        </div>
                                                    </div>

                                                    {/* Acceptance Criteria */}
                                                    <div className="sm:col-span-6">
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">Acceptance Criteria</label>
                                                        <div className="mt-2">
                                                            <textarea
                                                                rows={3}
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                value={formData.acceptance_criteria || ''}
                                                                onChange={(e) => setFormData({ ...formData, acceptance_criteria: e.target.value })}
                                                            />
                                                        </div>
                                                    </div>

                                                    {/* Requestor & Assignee */}
                                                    <div className="sm:col-span-3">
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">Requestor</label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                value={formData.requestor || ''}
                                                                onChange={(e) => setFormData({ ...formData, requestor: e.target.value })}
                                                            />
                                                        </div>
                                                    </div>
                                                    <div className="sm:col-span-3">
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">Assignee</label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                value={formData.assignee || ''}
                                                                onChange={(e) => setFormData({ ...formData, assignee: e.target.value })}
                                                            />
                                                        </div>
                                                    </div>

                                                    {/* Source Content (Read Only) */}
                                                    <div className="sm:col-span-6">
                                                        <label className="block text-sm font-medium leading-6 text-gray-900">Source Content</label>
                                                        <div className="mt-2 p-3 bg-gray-50 rounded-md text-sm text-gray-700 max-h-40 overflow-y-auto">
                                                            <pre className="whitespace-pre-wrap font-sans">
                                                                {JSON.stringify(request?.source_content, null, 2)}
                                                            </pre>
                                                        </div>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>

                                <div className="mt-5 sm:mt-4 sm:flex sm:flex-row-reverse">
                                    {!isCreating && (
                                        <>
                                            <button
                                                type="button"
                                                className="inline-flex w-full justify-center rounded-md bg-green-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-green-500 sm:ml-3 sm:w-auto"
                                                onClick={handleApprove}
                                            >
                                                Approve
                                            </button>
                                            <button
                                                type="button"
                                                className="inline-flex w-full justify-center rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 sm:ml-3 sm:w-auto"
                                                onClick={handleReject}
                                            >
                                                Reject
                                            </button>
                                        </>
                                    )}
                                    <button
                                        type="button"
                                        className="inline-flex w-full justify-center rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 sm:ml-3 sm:w-auto"
                                        onClick={handleSave}
                                    >
                                        {isCreating ? 'Create Request' : 'Save Changes'}
                                    </button>
                                    <button
                                        type="button"
                                        className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:mt-0 sm:w-auto"
                                        onClick={onClose}
                                    >
                                        Cancel
                                    </button>
                                </div>
                            </Dialog.Panel>
                        </Transition.Child>
                    </div>
                </div>
            </Dialog>
        </Transition.Root>
    );
}
